@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Poppins;
  margin-inline: 1.5rem;
}

.container {
  max-width: 1400px;
  margin-inline: auto;
}

.heading {
  font-size: 2.5rem;
  margin: 1.5rem 0;
}

.sort-btn {
  border: none;
  padding: 8px 16px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  margin: 10px 5px;
}

.filter-list {
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 10px 5px;
  cursor: pointer;
}

.filter-list:focus {
  outline: none;
  border-color: #007bff;
}
